@keyframes loadingSpinAround {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

.kirki-settings-page .button.is-loading {
  pointer-events: none;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  color: #0000 !important;
}

.kirki-settings-page .button.is-loading:after {
  content: "";
  width: 1em;
  height: 1em;
  border: 2px solid #dbdbdb;
  border-radius: 290486px;
  animation: .5s linear infinite loadingSpinAround;
  display: block;
  position: absolute;
  border-color: #0000 #0000 #fff #fff !important;
}

.kirki-settings-page .submission-status {
  margin-left: 15px;
  line-height: 39px;
}

.kirki-settings-page .submission-status.is-hidden {
  display: none;
}

.kirki-settings-page .submission-status.is-success {
  color: #0fa953;
}

.kirki-settings-page .submission-status.is-error {
  color: tomato;
}

.kirki-featured-products > h2 {
  text-align: center;
  margin-bottom: 50px;
  font-size: 24px;
}

.kirki-featured-products .products {
  justify-content: space-between;
  display: flex;
}

.kirki-featured-products .products li {
  width: 32%;
}

.kirki-featured-products .products .subheadline {
  margin-top: -6px;
  font-weight: 600;
}

.kirki-featured-products .products img {
  max-width: 100%;
}

.kirki-featured-products .credit {
  text-align: center;
  opacity: .5;
  margin-top: 20px;
}

.heatbox {
  position: relative;
}

.heatbox .checkbox-label .is-disabled {
  opacity: .6;
}

.heatbox .heatbox-overlay.is-hidden {
  display: none;
}

.heatbox .heatbox-overlay {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #0000001a;
  position: absolute;
  top: 0;
  left: 0;
}

.kirki-pro-metabox span.badge {
  color: #fff;
  background: #6dbb7a;
  border-radius: 50px;
  padding: 4px 10px;
  font-size: 50%;
}

.kirki-pro-metabox .intro {
  background: #f1f1f1;
  margin-bottom: 0;
  padding: 20px;
}

.kirki-pro-metabox ul li {
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
  display: flex;
}

.kirki-pro-metabox ul li:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.kirki-pro-metabox h3 {
  margin-bottom: 8px;
  font-size: 18px;
}

.kirki-pro-metabox h3 a {
  color: #1d2327;
  text-decoration: none;
}

.kirki-pro-metabox h3 a:hover {
  color: #2051c4;
  text-decoration: underline;
}

.kirki-pro-metabox .dashicons-yes {
  color: #6dbb7a;
  margin-top: -3px;
  margin-right: 12px;
  font-size: 28px;
}

.kirki-pro-benefits, .kirki-pro-benefits li:last-child p {
  margin-bottom: 0;
}

